.App {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.notification {
  position: fixed;
  top: 20px;
  right: 20px;
  background: #333;
  color: white;
  padding: 15px 25px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  z-index: 1000;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    transform: translateX(400px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.container {
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  padding: 40px;
  max-width: 800px;
  width: 100%;
}

.title {
  font-size: 2.5rem;
  color: #667eea;
  text-align: center;
  margin-bottom: 30px;
  font-weight: bold;
}

.subtitle {
  font-size: 1.5rem;
  color: #555;
  text-align: center;
  margin-bottom: 20px;
}

.input-group {
  margin-bottom: 20px;
}

.input-group label {
  display: block;
  margin-bottom: 8px;
  color: #555;
  font-weight: 600;
}

.input-group input {
  width: 100%;
  padding: 12px 15px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s;
}

.input-group input:focus {
  outline: none;
  border-color: #667eea;
}

.btn {
  padding: 12px 30px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  margin: 5px;
}

.btn-primary {
  background: #667eea;
  color: white;
}

.btn-primary:hover {
  background: #5568d3;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.btn-secondary {
  background: #764ba2;
  color: white;
}

.btn-secondary:hover {
  background: #63408a;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(118, 75, 162, 0.4);
}

.btn-danger {
  background: #e74c3c;
  color: white;
}

.btn-danger:hover {
  background: #c0392b;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(231, 76, 60, 0.4);
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 15px;
  padding: 40px;
  color: white;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  margin: 20px 0;
  cursor: pointer;
  transition: transform 0.3s;
}

.card:hover {
  transform: scale(1.05);
}

.card.flipped {
  background: white;
  color: #333;
  cursor: default;
}

.card.flipped:hover {
  transform: none;
}

.player-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 15px;
  margin: 20px 0;
}

.player-card {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #ddd;
  text-align: center;
}

.player-card.is-host {
  border-color: #667eea;
  background: #e8eaf6;
}

.player-card.is-bot {
  border-color: #764ba2;
  background: #f3e5f5;
}

.player-card.is-current {
  border-color: #4caf50;
  background: #e8f5e9;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.config-section {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  margin: 20px 0;
}

.config-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
  padding: 10px;
}

.config-item label {
  font-weight: 600;
  color: #555;
}

.config-item input[type="number"],
.config-item input[type="checkbox"] {
  padding: 8px;
  border: 2px solid #ddd;
  border-radius: 5px;
}

.room-code {
  background: #667eea;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin: 20px 0;
  letter-spacing: 3px;
}

.word-display {
  font-size: 3rem;
  color: #667eea;
  font-weight: bold;
  margin: 30px 0;
  text-align: center;
  text-transform: uppercase;
}

.role-badge {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 20px;
  font-weight: bold;
  margin: 10px 0;
}

.role-badge.civil {
  background: #4caf50;
  color: white;
}

.role-badge.impostor {
  background: #e74c3c;
  color: white;
}

.word-list {
  margin: 20px 0;
}

.word-item {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  margin: 10px 0;
  border-left: 4px solid #667eea;
}

.word-item strong {
  color: #667eea;
}

.progress-text {
  text-align: center;
  color: #555;
  font-size: 18px;
  margin: 20px 0;
}

.voting-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 15px;
  margin: 20px 0;
}

.vote-card {
  background: white;
  border: 2px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s;
}

.vote-card:hover {
  border-color: #667eea;
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.vote-card.selected {
  border-color: #667eea;
  background: #e8eaf6;
}

.results-container {
  text-align: center;
}

.winner-banner {
  font-size: 3rem;
  font-weight: bold;
  margin: 30px 0;
  padding: 20px;
  border-radius: 15px;
}

.winner-banner.civils {
  background: #4caf50;
  color: white;
}

.winner-banner.impostors {
  background: #e74c3c;
  color: white;
}

.players-reveal {
  margin: 30px 0;
}

.players-reveal h3 {
  color: #555;
  margin-bottom: 20px;
}

.textarea-word {
  width: 100%;
  padding: 15px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 18px;
  resize: vertical;
  min-height: 80px;
  font-family: inherit;
}

.textarea-word:focus {
  outline: none;
  border-color: #667eea;
}

.button-group {
  display: flex;
  gap: 10px;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px 0;
}
